
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#gains .times-list {
  overflow: hidden;
  padding: 10px;
  background: #fff;
  border-bottom: 1px solid #ddd;
}
#gains .times-list li {
  float: left;
  padding: 5px 12px;
  background: #ddd;
  border-radius: 5px;
  margin-right: 15px;
}
#gains .time {
  background: #fff;
  padding: 10px;
  overflow: hidden;
  border-bottom: 1px solid #ddd;
}
#gains .time .fl {
  margin-right: 50px;
}
#gains .time .mr0 {
  margin-right: 0;
}
#gains .time input {
  width: 100px;
}
#gains .place {
  background: #fff;
  padding: 10px;
  overflow: hidden;
}
#gains .place .fl {
  margin-right: 50px;
}
#gains .place .mr0 {
  margin-right: 0;
  width: 200px;
}
#gains .gains-detail {
  margin-top: 20px;
}
#gains .gains-detail .tit {
  overflow: hidden;
  padding: 10px;
  background: #e2e9f3;
  border-top: 1px solid #ddd;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}
#gains .gains-detail .tit .fl {
  padding: 5px 0;
}
#gains .gains-detail .tit .tit-num {
  margin-left: 10px;
  background: #0f0;
  color: #fff;
  padding: 5px 10px;
  border-radius: 8px;
}
#gains .gains-detail .tit .fr {
  font-size: 20px;
}
#gains .gains-detail .list {
  background: #fff;
  border-bottom: 1px solid #ddd;
}
#gains .gains-detail .list li {
  overflow: hidden;
  padding: 10px;
}
#gains .gains-detail .list li .col {
  color: #999;
}
#gains .gains-detail .list li .fs {
  font-size: 13px;
}
#gains .gains-detail .sum {
  overflow: hidden;
  background: #fff;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}
#gains .gains-detail .sum .fr {
  text-align: right;
}
#gains .gains-detail .sum .fr .col {
  color: #999;
  font-size: 14px;
}
#gains .gains-detail .gif-use {
  overflow: hidden;
  background: #fff;
  padding: 10px;
}



